//== Paths

// Images

//== Fonts

// Family
$font-family-base: "Roboto Flex", sans-serif;
$font-family-secondary: "Aladin", sans-serif;

// Size
$font-size-base: 16px;
$font-size-secondary: 20px;

// Line-height
$font-line-height-base: 1;

// Weight
$thin: 100;
$extralight: 200;
$light: 300;
$regular: 400;
$medium: 500;
$semibold: 600;
$bold: 700;
$extra-bold: 800;
$black-weight: 900;

//== Site sizes
$site-width: 1440px;
$site-gutter: 160px;

//== Border radius
$card-border-radius: 28px;

//== Colors
$white: #ffffff;
$black: #000000;
$body-green: #343A19;
$title-yellow: #F8D77E;
$title-shadow: #BF801F;

//== Card
$card-background: linear-gradient(180deg, #424C16 0%, #313911 100%);
$card-border: 2px solid rgba(255, 213, 93, 0.20);

$color-text-primary: $black;