@import "reset";
@import "variables";
@import "mixins";

body {
  background-color: $body-green;
  color: $white;
}

.container,
.footer {
  max-width: 100%;
}

.global-container {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.container {
  position: relative;
  margin-top: 115px;
  display: flex;
  flex: 1 0 auto;
  flex-direction: column;
  //overflow: hidden;
}

.footer {
  flex: 0 0 auto;
}

