@import "src/scss/mixins";

.wrapper {
  position: relative;
}

.statsList {
  position: relative;
  display: grid;
  gap: 32px;
  grid-template-columns: repeat(3, 1fr);

  @include screen-md {
    grid-template-columns: repeat(2, 1fr);
  }
}

.coinImageTop {
  position: absolute;
  top: -75px;
  right: -70px;
  transform: scale(-1, 1);
}

.coinImageBottom {
  position: absolute;
  bottom: -110px;
  left: -110px;
  rotate: -5deg;
}

