@mixin gray-rounded-block {
  background-color: $gray-light-8;
  border-radius: 16px;
}

@mixin flex-align-justify-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin show-more-button {
  width: 100%;
  padding: 13px 26px;
  font: $medium 18px $font-family-secondary;
  line-height: 1.2;
}

@mixin popup-button {
  max-width: calc(50% - 9px);
  font: $medium 18px/1.2 $font-family-base;
  padding: 22px 48px;

  & + & {
    margin-left: 18px;
  }
}

@mixin screen-lg() {
  @media screen and (max-width: 1920px) {
    @content;
  }
}

@mixin screen-md() {
  @media screen and (max-width: 1400px) {
    @content;
  }
}

@mixin screen-sm() {
  @media screen and (max-width: 1240px) {
    @content;
  }
}

@mixin tablet-lg() {
  @media screen and (max-width: 991px) {
    @content;
  }
}

@mixin tablet-md() {
  @media screen and (max-width: 768px) {
    @content;
  }
}

@mixin tablet-sm() {
  @media screen and (max-width: 601px) {
    @content;
  }
}

@mixin phone-lg() {
  @media screen and (max-width: 480px) {
    @content;
  }
}

@mixin phone-sm() {
  @media screen and (max-width: 400px) {
    @content;
  }
}
